button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.btn-large,
.btn-primary,
.btn-secondary,
.btn {
  display: inline-block;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0 20px 20px 0;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 16px;
  line-height: 1.25;
  border-radius: 4px; }
  @media screen and (max-width: 767px) {
    
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"],
    .btn-large,
    .btn-primary,
    .btn-secondary,
    .btn {
      margin-bottom: 10px; } }
  
  button:focus,
  button:hover,
  input[type="button"]:focus,
  input[type="button"]:hover,
  input[type="reset"]:focus,
  input[type="reset"]:hover,
  input[type="submit"]:focus,
  input[type="submit"]:hover,
  .btn-large:focus,
  .btn-primary:focus,
  .btn-secondary:focus,
  .btn-large:hover,
  .btn-primary:hover,
  .btn-secondary:hover,
  .btn:focus,
  .btn:hover {
    text-decoration: none; }
  
  button:focus,
  input[type="button"]:focus,
  input[type="reset"]:focus,
  input[type="submit"]:focus,
  .btn-large:focus,
  .btn-primary:focus,
  .btn-secondary:focus,
  .btn:focus {
    outline: 3px solid #1799d2; }
  
  button:active,
  input[type="button"]:active,
  input[type="reset"]:active,
  input[type="submit"]:active,
  .btn-large:active,
  .btn-primary:active,
  .btn-secondary:active,
  .btn:active {
    box-shadow: none !important; }
  
  button a,
  input[type="button"] a,
  input[type="reset"] a,
  input[type="submit"] a, .btn-large a, .btn-primary a, .btn-secondary a,
  .btn a {
    display: inline-block;
    text-decoration: none;
    color: inherit; }


button,
input[type="button"],
input[type="reset"],
input[type="submit"],
a.btn,
.btn a, .btn-large a, .btn-primary a, .btn-secondary a,
a.btn-large,
a.btn-primary,
a.btn-secondary {
  padding: 10px 30px;
  transition: none; }

.btn-large {
  font-size: 18px;
  line-height: 1.22222;
  color: #ffffff;
  background-color: #0000b6; }
  .btn-large:focus, .btn-large:hover {
    color: #ffffff;
    background-color: #01006d; }

.btn-large a, a.btn-large {
  padding: 19px 35px 19px 40px; }
  .btn-large a:after, a.btn-large:after {
    content: ""; }
  .btn-large a:after, a.btn-large:after {
    display: inline-block;
    font-size: 12px;
