.btn-secondary {
  color: #000000;
  background-color: transparent;
  border: 1px solid #000000; }
  .btn-secondary:focus, .btn-secondary:hover {
    color: #ffffff;
    background-color: #000000; }

.btn-tertiary {
  color: #323232;
  background-color: #ffffff;
  border: 0; }
