    text-decoration: underline; }
    .btn-tertiary a:after {
      content: ""; }
    .btn-tertiary a:after {
      display: inline-block;
      font-size: 9px;
      color: inherit;
      margin-left: 10px;
      text-decoration: none; }
    .btn-tertiary a:hover {
      color: #1e2634; }

